
.slider{
    margin-top: 10px;
    height: 200px;

    button[class="slick-arrow slick-prev"]{
        
        left: 89%;
        top: -14%;

        &::before{
            padding: 14px 14px 12px 14px;
            background-color: rgb(20, 20, 20);
            border-radius: 12px;
            border: 1px solid #262626;
            color: rgb(243, 208, 86);
        }
    }
    button[class="slick-arrow slick-next"]{
        left: 96%;
        top: -14%;
        
        &::before{
            padding: 14px 14px 12px 14px;
            background-color: rgb(20, 20, 20);
            border-radius: 12px;
            border: 1px solid #262626;
            color: rgb(243, 208, 86);
        }
    }


    & ul{
        width: 130px;
        height: 20px;
        top: -12%;
        right: 6.5%;

        li[class="slick-active"] button::before{
            background-color: rgb(243, 208, 86);
            width: 130%;
            margin-left: -15%;
        }

       button::before{
            content: '';
            width: 100%;
            height: 5px;
            border-radius: 100px;
            background-color: #333
       }
    }
}

.actorContainer{
    padding: 15px 0px 20px 20px;

    div{
        display: flex;
        gap: 10px;
        width: 120px;
        padding: 8px;
        border: 1px solid #262626;
        background-color: #202020;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        border-radius: 12px;
        cursor: pointer;
        transition: 200ms linear;

        &:hover{
            scale: 1.1;

        }
    }

    .role{
        font-size: 12px;
        color: #999;
        margin: 0;
    }

    .name{
        font-size: 14px;
        margin: 0;
    }
    .img{
        border-radius: 12px;

        width: 100px;
        height: 120px;

    }
}


